<template>
    <div>
        <loading :active.sync="isLoading"></loading>              
        <!-- vif -->  
        <div class="my-5 row justify-content-center" v-if="cart.carts">
            <div class="col-md-6">
                <table class="table">
                <thead>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </thead>
                <tbody>
                    <tr v-for="item in cart.carts" :key="item.id" >
                    <td class="align-middle">
                        <button type="button" class="btn btn-outline-danger btn-sm"
                        @click="removeCartItem(item.id)">
                        <i class="far fa-trash-alt"></i>
                        </button>
                    </td>
                    <td class="align-middle">
                        {{ item.product.title }}
                        <div class="text-success" v-if="item.coupon">
                        
                        </div>
                    </td>
                    <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                    <td class="align-middle text-right">{{ item.final_total }}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                    <td colspan="3" class="text-right"></td>
                    <td class="text-right">{{ cart.total }}</td>
                    </tr>
                    <tr v-if="cart.final_total !== cart.total">
                    <td colspan="3" class="text-right text-success"></td>
                    <td class="text-right text-success">{{ cart.final_total }}</td>
                    </tr>
                </tfoot>
                </table>
                <div class="input-group mb-3 input-group-sm">
                <input type="text" class="form-control" v-model="coupon_code" placeholder="">
                <div class="input-group-append">
                    <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
                    
                    </button>
                </div>
                </div>
            </div>
        </div>
        <!-- vif --> 
        <!-- else -->         
        <div class="my-5 row justify-content-center" v-else>
            <div class="col-md-6">
                <table class="table">
                <thead>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </thead>
                <tbody>
                    <tr v-for="item in cart.carts" :key="item.id" >
                    <td colspan="4"></td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                    <td colspan="3" class="text-right"></td>
                    <td class="text-right"></td>
                    </tr>
                    <tr v-if="cart.final_total !== cart.total">
                    <td colspan="3" class="text-right text-success"></td>
                    <td class="text-right text-success"</td>
                    </tr>
                </tfoot>
                </table>
            </div>
        </div>
        <!-- else -->
                <!--FORM-->
        
        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="createOrder">
                <div class="form-group">
                <label for="useremail">Email</label>
                <input type="email" class="form-control" name="email" id="useremail" required
                    v-validate="'required|email'"
                    :class="{'is-invalid': errors.has('email')}"
                    v-model="form.user.email" placeholder=" Email">
                <span class="text-danger" v-if="errors.has('email')">
                    {{ errors.first('email') }}
                </span>
                </div>

                <div class="form-group">
                <label for="username"></label>
                <input type="text" class="form-control" name="name" id="username" required
                    :class="{'is-invalid': errors.has('name')}"
                    v-model="form.user.name" v-validate="'required'" placeholder="">
                <span class="text-danger" v-if="errors.has('name')"></span>
                </div>

                <div class="form-group">
                <label for="usertel"></label>
                <input type="tel" class="form-control" id="usertel"
                    v-model="form.user.tel" placeholder="">
                </div>

                <div class="form-group">
                <label for="useraddress"></label>
                <input type="address" class="form-control" name="address"
                    :class="{'is-invalid': errors.has('address')}"
                    id="useraddress" v-model="form.user.address" v-validate="'required'"
                    placeholder="">
                <span class="text-danger" v-if="errors.has('address')"></span>
                </div>

                <div class="form-group">
                <label for="useraddress"></label>
                <textarea name="" id="" class="form-control" cols="30" rows="10"
                    v-model="form.message"></textarea>
                </div>
                <div class="text-right">
                <button class="btn btn-danger"></button>
                </div>
            </form>
        </div>
        
        <!--FORM-->     
    </div>
</template>

<script>
import $ from 'jquery';

export default {

    data(){
        return {
            //
            cart: {},          
            //
            form: {
                user: {
                    name: '',
                    email: '',
                    tel: '',
                    address: '',
                },
                message: '',
            },
            //
            isLoading:false,
            status:{},
            coupon_code:'',
        }
    },
    methods:{
                getCart(){
                const api = `https://vue-course-api.hexschool.io/api/heylin/cart`;
                const vm = this;
                vm.isLoading=true;

                this.$http.get(api).then((response) => {
                    console.log('=========');
                    console.log(response.data);
                    console.log('=========');
                    
                    vm.cart = response.data.data;
                    vm.isLoading=false;
                        
                });
        },
        removeCartItem(pid){

            var del=confirm("?");
            if(del){
                const api = `https://vue-course-api.hexschool.io/api/heylin/cart/${pid}`;
                const vm = this;
                vm.isLoading=true;

                this.$http.delete(api).then((response) => {
                    console.log('=========');
                    console.log(response.data);
                    console.log('=========');
                    //
                    vm.getCart();
                    vm.isLoading=false;
                        
                });
            }
            else{

            }

        },
        addCouponCode(){
            const api = `https://vue-course-api.hexschool.io/api/heylin/coupon`;
            const vm = this;
            const coupon = {
                code:vm.coupon_code
            }

            vm.isLoading=true;
            this.$http.post(api,{data:coupon}).then((response) => {
                console.log('=========');
                console.log(response.data);
                console.log('=========');
                //
                vm.getCart();
                vm.isLoading=false;
                        
            });
        },
        createOrder(){
            const api = `https://vue-course-api.hexschool.io/api/heylin/order`;
            const vm = this;
            const form = vm.form;
            
            this.$validator.validate().then((result)=>{
                if(result){
                    vm.isLoading=true;
                    this.$http.post(api,{data:form}).then((response) => {
                        console.log('=========');
                        console.log('',response);
                        console.log('=========');
                        if(response.data.success)
                        {
                            vm.$router.push(`membercheckout/${response.data.orderId}`);
                        }
                        //
                        vm.getCart();
                        vm.isLoading=false;                                    
                    });
                }
                else{
                    console.log('');
                }
            });
        }
    },
    computed: {

    },
    created() {

        this.getCart();
        //
        //this.$bus.$emit('message:push','','success');
    },
}

</script>


